$darkBlue: rgb(20, 21, 24);
body {
  font-size: 15px;
  background-color: $darkBlue;
  font-family: "Open Sans", sans-serif;
}
div,
span,
p,
a,
nav,
ul,
li,
section,
header,
footer,
h1,
h2,
h3,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  width: 1250px;
  margin: 0 auto;
}
h1,
h2,
h3 {
  color: #ffffff;
  text-align: center;
}

.d-flex {
  display: flex;
}
