$lightBlue: rgb(46, 52, 66);
$hoverLightBlue: rgb(29, 32, 41);
.countries {
  &-wrapper {
    display: flex;
    flex-direction: column;
    flex-flow: wrap;
  }
  .country {
    margin: 3px;
    width: 24%;
    padding: 9px 0px 9px 10px;
    background-color: $lightBlue;
    color: rgb(193, 193, 193);
    border-radius: 2px;
    cursor: pointer;
    transition: all 0.05s ease-in-out;
    &:hover {
      color: #fff;
      background-color: $hoverLightBlue;
    }
  }
}

.react-select {
  width: 310px;
  color: rgb(218, 218, 218) !important;
  color: rgba(182, 255, 99, 0.2);
  margin: 5px;
  cursor: pointer;
  > div {
    background-color: $lightBlue;
    border: none;
    box-shadow: none;
    margin-top: 0;
    > div {
      div {
        &:hover {
          background-color: $hoverLightBlue !important;
        }
      }
    }
  }
  option {
    display: none !important;
  }
}
