$lightBlue: rgb(46, 52, 66);

.dashboard {
  color: #fff;
  display: flex;
  height: 100vh;

  .table {
    display: flex;
    margin: 0 auto;
  }

  .grid {
    background-color: $lightBlue;
    width: 150px;
    height: 150px;
    margin: 5px;
    text-align: center;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    transition: all 0.1s ease-in-out;

    &:hover {
      transform: scale(1.05);
    }
    h2 {
      display: inline-block;
      margin-top: 30px;
      font-size: 18px;
    }
    p {
      margin-top: 15%;
    }
  }
  .country-grid {
    display: block;
    color: #fff;
    width: 310px;
    height: 200px;
    cursor: pointer;

    h2 {
      margin: 0;
      font-size: 25px;
      line-height: 200px;
    }
  }
}
